import "highlight.js/styles/atom-one-dark.css";
import hljs from "highlight.js";
import React, { useState, useEffect, useCallback, useRef } from "react";
import {
  Card,
  CardContent,
  TextField,
  Typography,
  IconButton
} from "@material-ui/core";
import SendIcon from "@material-ui/icons/Send";
import FileCopyIcon from "@material-ui/icons/FileCopy";
import DeleteIcon from "@material-ui/icons/Delete";
import Brightness4Icon from "@material-ui/icons/Brightness4";
import { makeStyles } from "@material-ui/styles";

const useStyles = makeStyles({
  root: {
    height: "100vh",
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
    backgroundColor: "#282c34"
  },
  chatCard: {
    width: "100%",
    height: "80%", // Increased height of chat card
    margin: "auto",
    marginTop: 0,
    backgroundColor: "#282c34"
  },
  title: {
    fontSize: "1.5rem",
    color: "#ffffff"
  },
  inputContainer: {
    display: "flex",
    alignItems: "center",
    marginTop: "8px",
    marginBottom: "1%",
    padding: "0 16px"
  },
  inputField: {
    flexGrow: 1,
    marginRight: "8px",
    backgroundColor: "#ffffff",
    borderRadius: "25px"
  }
});

const Chat = () => {
  const classes = useStyles();
  const [userMessage, setUserMessage] = useState("");
  const [chatMessages, setChatMessages] = useState([]);
  const [isButtonClicked, setIsButtonClicked] = useState(false);
  const chatContainerRef = useRef(null);

  const handleUserMessageChange = (event) => {
    setUserMessage(event.target.value);
  };

  const handleSendMessage = () => {
    if (userMessage) {
      const botMessage = { message: "Typing...", isBot: true };
      setChatMessages((prevChatMessages) => [
        ...prevChatMessages,
        { message: userMessage, isBot: false },
        botMessage
      ]);
      setIsButtonClicked(true);
    }
  };

  const fetchBotMessage = useCallback(() => {
    fetch(
      `https://walrus-app-hodhq.ondigitalocean.app/androidJava?q=${userMessage}`
    )
      .then((response) => response.text())
      .then((data) => {
        const botMessage = { message: data, isBot: true };
        setChatMessages((prevChatMessages) => [
          ...prevChatMessages.slice(0, -1),
          botMessage
        ]);
      })
      .catch((error) => {
        console.error(error);
      });
  }, [userMessage]);

  useEffect(() => {
    if (isButtonClicked) {
      fetchBotMessage();
      setIsButtonClicked(false);
      setUserMessage("");
    }
  }, [isButtonClicked, fetchBotMessage]);

  useEffect(() => {
    if (chatContainerRef.current) {
      chatContainerRef.current.scrollTop =
        chatContainerRef.current.scrollHeight;
    }
  }, [chatMessages]);

  const formatMessage = (message) => {
    return message.split("```").map((snippet, index) => {
      if (index % 2 === 1) {
        const languageMatch = snippet.match(/^(\w+)\n/);
        const language = languageMatch ? languageMatch[1] : null;
        const cleanedSnippet = snippet.replace(/^\w+\n/, "");
        const highlightedSnippet = hljs.highlightAuto(
          cleanedSnippet,
          language ? [language] : null
        ).value;
        return (
          <div key={index} style={{ position: "relative" }}>
            <pre
              style={{
                overflowX: "auto",
                whiteSpace: "pre-wrap",
                wordWrap: "break-word"
              }}
              dangerouslySetInnerHTML={{ __html: highlightedSnippet }}
            ></pre>
            <IconButton
              style={{
                position: "absolute",
                top: 0,
                right: 0,
                padding: 0,
                fontSize: "0.8rem",
                color: "#00b0ff" // Changed color of copy icon
              }}
              onClick={() => {
                navigator.clipboard.writeText(cleanedSnippet);
              }}
            >
              <FileCopyIcon fontSize="small" />
            </IconButton>
          </div>
        );
      } else {
        return <span key={index}>{snippet}</span>;
      }
    });
  };

  const clearChat = () => {
    setChatMessages([]);
  };

  const changeTheme = () => {
    // Implement theme change logic here
  };

  return (
    <div className={classes.root}>
      <IconButton
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          padding: 0,
          fontSize: "0.8rem",
          color: "#00b0ff"
        }}
        onClick={changeTheme}
      >
        <Brightness4Icon fontSize="small" />
      </IconButton>
      <Card className={classes.chatCard} elevation={0}>
        <CardContent>
          <Typography className={classes.title} align="center" gutterBottom>
            Riya
          </Typography>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              height: "70vh",
              overflow: "auto"
            }}
            ref={chatContainerRef}
          >
            {chatMessages.map((message, index) => (
              <div
                key={index}
                style={{
                  display: "flex",
                  justifyContent: message.isBot ? "flex-start" : "flex-end", // Align user's message to the right
                  marginBottom: "8px"
                }}
              >
                <Card
                  style={{
                    width: "auto",
                    maxWidth: "85%",
                    backgroundColor: message.isBot ? "#4b4f56" : "#3b3e44"
                  }}
                >
                  <CardContent
                    style={{
                      padding: "5px"
                    }}
                  >
                    <Typography
                      variant="body1"
                      style={{ fontSize: 14, color: "#ffffff" }}
                    >
                      {formatMessage(message.message)}
                    </Typography>
                  </CardContent>
                </Card>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>
      <div className={classes.inputContainer}>
        <IconButton color="primary" onClick={clearChat}>
          <DeleteIcon />
        </IconButton>
        <TextField
          className={classes.inputField}
          label="Type a message"
          value={userMessage}
          onChange={handleUserMessageChange}
          autoComplete="off"
          autoFocus
          multiline
          minRows={1} // Changed from rows to minRows
          inputProps={{
            style: {
              overflow: "auto",
              resize: "none"
            }
          }}
          InputLabelProps={{
            style: {
              color: "#000000"
            }
          }}
          variant="filled"
        />
        <IconButton color="primary" onClick={handleSendMessage}>
          <SendIcon />
        </IconButton>
      </div>
    </div>
  );
};
export default Chat;
